@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Sora:wght@100..800&display=swap');

.update-php .wrap {
	max-width: 100%;
}

.jkit-notice-banner {
	font-family: "Inter", sans-serif;
	position: relative;
	display: flex;
	margin: 10px 20px 20px 0px;
	padding: 0;
	border: 1px solid #c3c4c7;
	border-left-width: 0;

	.jkit-notice-banner-logo {
		background: #ecf0f8;
		padding: 20px 15px;
		border-left: 4px;
		border-right: 0;
		border-style: solid;
		border-image: linear-gradient(to bottom, #3f3bf7, #5cd0da) 1 100%;

		img {
			width: 24px;
			height: 24px;
		}
	}

	.jkit-notice-banner-content {
		padding: 20px 20px 25px 20px;
		width: 100%;

		h2 {
			font-family: "Inter", sans-serif;

			margin-top: 0;
			margin-bottom: 10px;
		}

		.jkit-notice-action {
			display: flex;
			justify-content: space-between;
			margin-top: 20px;

			a {
				text-decoration: none;
			}

			.jkit-notice-action-button,
			.jkit-fix-action-button {
				color: #fff;
				background: #1b67a5;
				border-radius: 3px;
				cursor: pointer;
				padding: 7px 15px;
				text-decoration: none;
			}

			.jkit-notice-action-right {
				display: flex;
				align-items: center;
				gap: 5px;

				a {
					display: flex;
					gap: 7px;
					text-decoration: none;
					color: #1b67a5;
					justify-content: center;
					align-items: center;

					&:before {
						display: block;
						font-size: 16px;
					}
				}
			}

			.jkit-notice-action-left {
				display: flex;
				gap: 20px;
				vertical-align: middle;
				align-items: center;

				span {
					color: #ffb900;
				}

				.jkit-notice-close {
					display: flex;
					align-items: center;
					gap: 7px;

					svg * {
						fill: #2271b1;
					}

					&:hover {
						svg * {
							fill: #135e96;
						}
					}
				}
			}
		}
	}

	.jkit-banner-close {
		position: absolute;
		top: 20px;
		right: 20px;

		svg {
			width: 10px;
			height: 10px;
			cursor: pointer;
		}

		&:hover {
			svg * {
				fill: #fd3d31;
			}
		}
	}
}


.jkit-upgrade-banner {
	position: relative;
	display: flex;
	overflow: hidden;
	padding: 48px;
	background-image: url('../../svg/upgrade-to-pro-background.svg');
	background-repeat: no-repeat;
	background-size: cover;
	border: unset;

	.jkit-banner-content {
		z-index: 2;

		display: flex;
		align-items: flex-start;
		flex-direction: column;
	}

	h1 {
		font-family: 'Sora', sans-serif;
		font-size: 32px;
		line-height: 1.2;
		font-weight: 700;
		margin-bottom: 8px;
		color: #fff;
	}

	.jkit-pro-text-highlight {
		font-weight: 800;
		background: linear-gradient(90deg, #D15DF7 47.6%, #3B98FF 68.27%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		background-clip: text;
	}

	p {
		font-family: "Inter", sans-serif;
		color: #b4b6bd;
		font-weight: 400;
		font-size: 16px;
		line-height: 1.4;
		margin-bottom: 30.46px;
		max-width: 453px;
		color: #FFFFFFBF;
	}

	.jkit-banner-cta-button-wrapper {

		z-index: 10;
		position: relative;
		cursor: pointer;
		transition: all 0.5s ease;
		background: radial-gradient(112.43% 113.24% at 49.77% 0%, #BA70FF 3.52%, #2F47FF 100%);
		border-radius: 10px;
		overflow: hidden;

		&::before {
			content: "";
			position: absolute;
			inset: 0;
			padding: 2px;
			background: linear-gradient(180deg, #9760ff 0, rgba(255, 255, 255, .1) 100%);
			box-shadow: 0 4px 4px -4px rgba(0, 0, 0, .2);
			border-radius: 10px;
			mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
			mask-composite: exclude;
		}

		&:hover {
			transform: scale(0.94);
		}

		.jkit-banner-cta-button {
			font-family: "Inter", sans-serif;
			color: white;
			display: flex;
			padding: 12px 22px;
			justify-content: center;
			align-items: center;
			font-size: 14px;
			font-weight: 600;
			gap: 10px;
			border-radius: 10px;
			transition: all 0.3s ease;
			text-decoration: none;
			position: relative;
			z-index: 999;

		}
	}



	.jkit-crown-icon {
		width: 12px;
	}

	.jkit-banner-arrow {
		position: absolute;
		left: 253px;
		bottom: 12%;
		transform: translateY(-50%);
		width: 88px;
		z-index: 2;
	}

	.jkit-banner-hero {
		position: absolute;
		width: 452px;
		right: 20px;
		bottom: 0;
	}

	.jkit-btn-close-button {
		position: absolute;
		top: 10px;
		right: 10px;
		background: #ffff;
		color: #99a3a9;
		border: none;
		font-size: 8px;
		cursor: pointer;
		width: 12px;
		height: 12px;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 3;
	}

	.jkit-banner-premium-demo {
		position: absolute;
		bottom: 0;
		right: 20px;
		display: inline-flex;
		justify-content: flex-end;

		svg {
			width: 100%;
			height: 100%;
		}

		@media screen and (max-width: 1024px) {
			display: none;
		}
	}
}